<template>
    <el-container>
        <!---->
        <el-aside width="300px">
            <el-card style="width:250px;height:300px">
                <div class="user-avatar">
                    <el-image fit="cover" :src="avatar" style="width:100%">
                        <div slot="error">
                            <i class="el-icon-picture-outline"></i>
                        </div>
                        <div slot="placeholder">
                            <img :src="defaultImage" alt="..">
                        </div>
                    </el-image>
                </div>
                <div class="avatar-button">
                    <el-upload :limit="1" accept="jpg,png,jpeg" action="https://baidu.com"><!--TODO v3.0action-->
                        <el-button type="text" size="medium" @click="handleUpload"></el-button>
                    </el-upload>
                </div>
            </el-card>
        </el-aside>
        <el-main>
            <!---->
            <el-form :model="profile" label-width="120px" label-position="right" ref="profile" :rules="profileRules"
                     v-loading="loading">
                <el-form-item label="" prop="username">
                    <el-input v-model="profile.username" style="width:50%"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input type="email" v-model="profile.email" class="input_width" required></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="profile.changePassword"></el-switch>
                </el-form-item>
                <el-form-item label="" prop="oldPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.oldPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password></el-input>
                </el-form-item>
                <el-form-item label="" prop="newPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.newPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password minlength="6" maxlength="26"
                              show-word-limit></el-input>
                </el-form-item>
                <el-form-item label="" prop="vnewPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.vnewPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password></el-input>
                </el-form-item>
                <el-row :gutter="6">
                    <!---->
                    <el-col :span="6">
                        <el-form-item>
                            <el-button type="primary" @click.prevent="handleUpdate"></el-button>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" v-if="role===1">
                        <el-row>
                            <el-col :push="19" :span="6">
                                <el-button type="warning" @click="dialogVisible=true"></el-button>
                            </el-col>
                        </el-row>
                        <el-dialog title="" :visible.sync="dialogVisible">
                            <Register/>
                        </el-dialog>
                    </el-col>
                </el-row>
            </el-form>
        </el-main>
    </el-container>
</template>

<script>
    import Register from "../Register";

    export default {
        name: "AboutMe",
        components: {Register},
        data() {
            const validName = (rule, value, callback) => {
                if (!value.match(/^[a-zA-Z\u4E00-\u9FA5]{2,16}$/)) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            };

            const validOldPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            };

            const validNewPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else if (value.length < 6 || value.length > 26) {
                    callback(new Error("6-26."));
                } else {
                    callback();
                }
            };

            const validPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else if (value !== this.profile.newPassword) {
                    callback(new Error("!"));
                } else {
                    callback();
                }
            };

            return {
                loading: false,
                role: 3,
                dialogVisible: false,
                defaultImage: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
                avatar: "https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png",
                profile: {
                    username: "",
                    email: "",
                    changePassword: false,
                    oldPassword: "",
                    newPassword: "",
                    vnewPassword: "",
                },

                profileRules: {
                    username: [
                        {required: true, message: "", trigger: 'blur'},
                        {validator: validName, trigger: 'blur'}
                    ],
                    email: [
                        {required: true, message: "", trigger: 'blur'}
                    ],
                    oldPassword: [
                        {validator: validOldPass, trigger: 'blur'}
                    ],
                    newPassword: [
                        {validator: validNewPass, trigger: 'blur'},
                    ],
                    vnewPassword: [
                        {validator: validPass, trigger: 'blur'}
                    ]
                }
            }
        },

        methods: {
            /**
             * 
             * @param api
             * @param data
             * @returns {Promise<void>}
             */
            async editInfo(api, data) {
                await this.axios.put(api, data)
                    .then(response => {
                        if (response.data.status === "success") {
                            this.$message.success("");
                            window.location.reload();
                        } else {
                            this.$message.error(response.data.reason);
                        }
                    })
                    .catch(e => {
                        console.error(e);
                        this.$message.error("")
                    })
            },

            /**
             * 
             */
            handleUpdate() {
                this.$refs['profile'].validate(valid => {
                    //
                    if (valid) {
                        //
                        this.$confirm("", "", {
                            confirmButtonText: "",
                            cancelButtonText: "",
                            type: "warning"
                        })
                        //
                            .then(() => {
                                this.loading = true;
                                this.editInfo("/api/editUserInfo", this.profile);
                                this.loading = false;
                            })
                            //
                            .catch(() => {
                                this.$message.info("");
                            });
                    }
                    //
                    else {
                        console.warn("");
                        return false;
                    }
                });
            },

            handleUpload() {//TODO v3.0

            }
        },

        async mounted() {
            //TODO v3.0
            await this.axios.get("/api/getUserInfo")
                .then(response => {
                    console.log("");
                    this.profile.username = response.data.username;
                    this.profile.email = response.data.email;
                    this.role = response.data.role;
                })
                .catch(e => {
                    console.error(e);
                    this.$message.error("");
                })
        }
    }
</script>

<style scoped>
    .input_width {
        width: 50%;
    }

    .user-avatar {
        padding: 0 auto;
    }

    .avatar-button {
        margin: 10px auto;
        text-align: center;
    }
</style>