<template>
    <div>
        <el-table :data="stockList" border style="width: 100%">
            <el-table-column prop="stockId" label="ID" v-if="false"></el-table-column>
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="table-expand">
                        <el-form-item label="" v-show="!!props.row.productDescription.color">
                            <span v-text="translateColor(props.row.productDescription.color)"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="!!props.row.productDescription.outlook">
                            <span v-text="props.row.productDescription.outlook"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="!!props.row.productDescription.description">
                            <span v-text="props.row.productDescription.description"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasMemory(props.row)">
                            <span v-text="props.row.productDescription.memory"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasStorage(props.row)">
                            <span v-text="props.row.productDescription.storage"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasNote(props.row)">
                            <span v-text="props.row.note"></span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column prop="dateTime" label="" width="160" sortable></el-table-column>
            <el-table-column prop="productName" label="" min-width="150"></el-table-column>
            <el-table-column prop="productType[0]" label="" :formatter="translateType" :filters="typeFilter"
                             :filter-method="filterType"></el-table-column>
            <el-table-column prop="price" label="" sortable></el-table-column>
            <el-table-column prop="num" label="" sortable></el-table-column>
            <el-table-column prop="total" label="" sortable></el-table-column>
            <el-table-column prop="creator" label=""></el-table-column>
            <el-table-column prop="contact" label="" width="150"></el-table-column>
            <el-table-column prop="platform" label=""></el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleEdit(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next,jumper" :page-size="50" :total="stockNum"
                       :current-page.sync="currentPage" @current-change="changePage">
        </el-pagination>
    </div>
</template>

<script>
    import product from "../../utils/productMap";

    export default {
        name: "StockList",
        data() {
            return {
                stockList: [],
                currentPage: 1,
                stockNum: 0,
                typeFilter: [
                    {text: "", value: "Phone"},
                    {text: "", value: "Pad"},
                    {text: "", value: "Computer"},
                    {text: "", value: "Accessories"},
                    {text: "", value: "EarPhones"},
                    {text: "", value: "Other"},
                ]
            }
        },
        methods: {
            async getStockList(page) {
                await this.axios.post("/api/getStocks", {page: page})
                    .then(response => {
                        this.stockList = response.data.stockList;
                        this.stockNum = response.data.stockNum;
                        console.log("");
                    })
                    .catch(e => {
                        console.error(e);
                        this.$message.error("");
                    })
            },

            async changePage(currentPage) {
                this.currentPage = currentPage;
                this.getStockList(this.currentPage);
            },

            handleEdit(row) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.$router.push({path: "/dashboard/editStock", query: {stockId: row.stockId}});
                    })
                    .catch(e => {
                        console.error(e);
                    })
            },

            filterType(value, row) {
                return row.productType[0] === value;
            },

            translateColor(color) {
                return product.colorMap[color];
            },

            translateType(row) {
                let typeMap = product.typeMap;
                if (row.productType.length === 1) {
                    return typeMap[row.productType[0]]
                }
                if (row.productType.length === 2) {
                    return typeMap[row.productType[1]] + typeMap[row.productType[0]]
                }
                return typeMap[row.productType[1]] + typeMap[row.productType[2]] + typeMap[row.productType[0]];
            },

            hasNote(row) {
                return !!row.note;
            },
            hasMemory(row) {
                return row.productType === "Phone" || row.productType === "Computer";
            },
            hasStorage(row) {
                return row.productType === "Phone" || row.productType === "Computer" || row.productType === "Pad";
            }
        },

        mounted() {
            this.getStockList(1);
        }
    }
</script>

